<template>
  <!-- footer start -->
    <footer class="cart-footer">
        <div class="left-item">
            <a href="javascript:void(0);" :class='!sAll?"allitem":"allitem allitem-active"' @click="selectAll"></a>
            <span>全选</span>
        </div>
        <div class="right-item">
            <div class="total-block">
                <span class="tit">合计：</span>
                <span class="price-num">￥{{totalPrice}}.00</span>
            </div>
            <a href="javascript:void(0);" class="uploadCart">
                结算
            </a>
        </div>
    </footer>
    <!-- footer end -->
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    export default{
        computed:mapGetters(['totalPrice','sAll']),
        methods:mapActions(['selectAll'])
    }
</script>


