import Vue from 'vue'
import Vuex from 'vuex'

import Cart from './modules/cart'

Vue.use(Vuex)

const state={
    count:0
}
const getters={
    count(state){
        return state.count;
    }
}
const mutations={
    increment(state){
        state.count++;
    }
}
const actions={
    increment:({commit})=>{
        commit('increment');
    }
}
export default new Vuex.Store({
    modules:{
        Cart
    },
    state,getters,mutations,actions
})



