<template>
  <footer>
    <div class="f-wrapper">
        <router-link :to=data.router class='item-lk' v-for="data in pageDatas" :key="data.name">
            <div class="icon-wrap">
                <icon :name=data.icon></icon>  
            </div>
            <div class="title">
                <span>{{data.name}}</span>
            </div>
        </router-link>
    </div>
  </footer>
</template>

<style>
  .icon-wrap>svg{
    position: absolute;
    height: 100%;
    width: 100%;
  }
</style>

<script>
export default {
  data() {
    return {
      pageDatas: [
        {
          name: "首页",
          router: "/home",
          icon:
            'home'
        },
        {
          name: "物流",
          router: "##",
          icon:
            'deliver'
        },
        {
          name: "购物车",
          router: "/cart",
          icon:
            'cart'
        },
        {
          name: "我的淘宝",
          router: "/myAccount",
          icon:
            'cc-head-o'
        },
        {
          name: "更多",
          router: "##",
          icon:
            'more'
        }
      ]
    };
  }
};
</script>


