
import Axios from 'axios';
import Vue from 'vue'
const state={
    cartNum:0,
    editArr:[],
    product:[],
    selectIt:[],
    sAll:false,
    totalPrice:0
};

const getters={
    cartNum(state){/* 购物车商品总数 */
        return state.cartNum;
    },
    product(state){/* 获取商品数据 */
        return state.product;
    },
    sAll(state){/* 选择 */
        return state.sAll;
    },
    totalPrice(state){/* 总价 */
        var selectIt=state.selectIt;
        state.totalPrice = 0;
        state.product.forEach(function (item,index) {
            console.log(index,item)
            if(selectIt[index]){
                state.totalPrice += item.p_now_price * item.p_num;
            }
            
        });
        return state.totalPrice;
    },
    editArr(state){
        return state.editArr;
    },
    selectIt(state){
        return state.selectIt;
    },
    total(state){
        
    }
};

const mutations={
    setCartNum(state){
        state
    },
    saveDatas(state,datas){/* 保存数据 */
        state.product=datas;
        state.cartNum=datas.length;
        for(var i=0;i<datas.length;i++){
            state.editArr[i]=false;
            state.selectIt[i]=false;
        }
    },
    isSelect(state,idx){/* 选中状态 */
        var its=state.selectIt;
        Vue.set(its,idx,!its[idx]);
    },
    selectAll(state){/* 全选状态 */
        let converts=false;
        let datas=state.selectIt;
        let sll=state.sAll;
        converts=!sll;
        for(var i=0;i<datas.length;i++){
            Vue.set(datas,i,converts);
        }
        state.sAll=!state.sAll;
    },
    editItems(state,idx){/* 编辑状态 */
        var edit=state.editArr;
        Vue.set(edit,idx,!state.editArr[idx]);
    },
    del(state,idx){/* 删除商品 */
        if (idx == -2) {
            state.product = [];
            return;
        }
        Vue.set(state.editArr,idx,false);
        state.selectIt[idx]=false;
        state.product.splice(idx, 1);
        state.thisIndex="";
        state.cartNum=state.product.length;
    },
    addNum(state,idx){/* 增加数量 */
        state.product[idx].p_num++;
    },
    decNum(state,idx){/* 减少数量 */
        if (state.product[idx].p_num <= 1) {
            if (confirm('确认要删除该物品吗')) {
                this.commit('del',idx);
            }
            return;
        }
        state.product[idx].p_num--;
    }

};

const actions={
    getDatas:({commit})=>{/* 请求购物车数据 并保存 */
        Axios.get("/static/json/cart.json").then(datas => {
            let data = datas.data.list;
            commit('saveDatas',data);
        });
    },
    selectThem({commit},index){/* 选择商品 */
        commit('isSelect',index);
    },
    selectAll:({commit})=>{
        commit('selectAll');
    },
    editItems:({commit},index)=>{
        commit('editItems',index);
    },
    del:({commit},index)=>{
        commit('del',index);
    },
    addNum:({commit},index)=>{
        commit('addNum',index);
    },
    decNum:({commit},index)=>{
        commit('decNum',index);
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}


