<template>
  <header id='idx_header' class="cart">
        <div href="javascript:void(0);" class="back-lk" @click='back'>
            <i class="fa fa-chevron-left"></i>
        </div>
        <div class="header-title">
            <span>购物车({{ cartNum }})</span>
        </div>
        <div class="right-box"></div>
    </header>
</template>

<script>
import {mapGetters} from 'vuex'
    export default{
        data(){
            return{

            }
        },
        methods:{
            back() {/* 后退 */
                this.$router.go(-1);
            }
        },
        computed:mapGetters([
            'cartNum'
        ])
    }
</script>



