<template>
<div>
    <!-- header start -->
    <cart-header></cart-header>
    <!-- header end -->
    <!-- productList start -->
    <cart-body></cart-body>
    <cart-footer></cart-footer>    
    <!-- modal end -->
</div>
  
    
</template>
<style lang="css">
    @import '../../assets/css/cart.css';
</style>


<script>
import Axios from "axios";
import {mapGetters,mapActions} from 'vuex';

import Header from './Header';
import Body from './Body';
import Footer from './Footer'

export default {
  name: "cart",
  data() {
    return {
      product: [],/* 商品数据 */
      /* 当前购物车商品数量 */
      editArr:[],/* 商品是否为编辑状态 */
      selectIt:[],/* 商品是否为选择状态 */
      thisIndex:'',/* 当前操作的列表索引 */
      seen:false,/* 模态框是否显示 */
      sll:false,/* 是否选择所有商品 */
      totalPrice:0/* 结算价格 */
    };
  },
  methods: {
      ...mapActions(['getDatas']),
    selectThem(index){/* 选择商品 */
        var its=this.selectIt;
        its[index]?this.$set(its,index,false):this.$set(its,index,true);
    },
    editItems(index){/* 编辑数量 */
        var edit=this.editArr;
        edit[index]?this.$set(edit,index,false):this.$set(edit,index,true);
    },
    decNum(index){/* 减少数量 */
        if (this.product[index].p_num <= 1) {
                if (confirm('确认要删除该物品吗')) {
                    this.remove(index);
                }
                return;
            }
            this.product[index].p_num--;
    },
    remove(idx) { /* 删除商品 */
            if (idx == -2) {
                this.product = [];
                return;
            }
            this.$set(this.editArr,idx,false);
            this.seen=false;
            this.selectIt[idx]=false;
            this.product.splice(idx, 1);
            this.thisIndex="";
            this.cartNum=this.product.length;
    },
    addNum(idx){/* 增加数量 */
        this.product[idx].p_num++;
    },
    selectAll(){/* 选择所有商品 */
        let converts=false;
        let datas=this.selectIt;
        let sll=this.sll;
        if(sll===true){
            converts=false;
        }else if(sll===false){
            converts=true;
        }
        for(var i=0;i<datas.length;i++){
            this.$set(datas,i,converts);
        }
        this.sll?this.sll=false:this.sll=true;
    }
  },
  beforeCreate(){
     
  },
  created() {/* 页面渲染完毕时 载入数据 */
     this.getDatas();
  },
    watch: {/*  */
        toCounts: function (val, oldVal) {
            this.totalPrice = val;
        }
    },
    computed: {
        toCounts: function () {
            var _this = this;
            var selectIt=this.selectIt;
            _this.totalPrice = 0;
            _this.product.forEach(function (item,index) {
                console.log(index,item)
                if(selectIt[index]){
                    _this.totalPrice += item.p_now_price * item.p_num;
                }
                
            })
            return _this.totalPrice;
        },
        ...mapGetters(['cartNum'])
    }
    ,components:{
        'cart-header':Header,
        'cart-body':Body,
        'cart-footer':Footer
    }
};
</script>



