<template>
    <section>
        <div class="function-block">
            <dl>
                <dd>
                    <a href="##">
                        <i>
                            <icon name="pay"></icon>
                        </i>
                        代付款
                    </a>
                </dd>
                <dd>
                    <a href="##">
                        <i><icon name="send"></icon></i>
                        待发货
                    </a>
                </dd>
                <dd>
                    <a href="##">
                        <i><icon name="deliver"></icon></i>
                        待收货
                    </a>
                </dd>
                <dd>
                    <a href="##">
                        <i>
                            <icon name="flower_2e"></icon>
                        </i>
                        待评价
                    </a>
                </dd>
                <dd>
                    <router-link to="">
                        <i>
                            <icon name="refund"></icon>
                        </i>
                        售后/退款
                    </router-link>
                </dd>
            </dl>

        </div>
        <div class="all-orders list-item">
            <div class="icon">
                <i>
                    <icon name="form"></icon>
                </i>
            </div>
            <div class="title">
                <p>全部订单</p>
            </div>
            <div class="tips">
                <p>查看全部订单</p>
                <i>
                    <icon name="go"></icon>
                </i>
            </div>
        </div>
        <div class="items">
            <router-link class="list-item" :to=data.path  v-for="data in itemsList" :key='data.title'>
                <div class="icon">
                    <i :style=data.bg>
                        <icon :name=data.icon></icon>
                    </i>
                </div>
                <div class="title">
                    <p>{{data.title}}</p>
                </div>
                <div class="tips">
                    <i>
                        <icon name="go" style="color: #9c9c9c;"></icon>
                    </i>
                </div>
            </router-link>
        </div>
    </section> 
</template>

<style lang="scss" scoped>
section {
    position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 4vh;
  width: 100%;
  font-size: 1rem;
}
.function-block {
  position: relative;
  background-color: #fff;
  width: 100%;
  border-bottom: 1px solid #ccc;
  dl {
    display: flex;
    flex-direction: row;
    > dd {
      flex-grow: 1;
      > a {
        padding: 2vh 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        > i {
          display: inline-block;
          position: relative;
          width: 5vh;
          height: 5vh;
          > svg {
            color: #888;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
.list-item {
  position: relative;
  display: flex;
  padding:0 2vh;
  height: 3rem;
  flex-direction: row;
  box-sizing: border-box;
  background-color: #fff;
  > .icon,> .title, > .tips{
      display: flex;
      align-items: center;
  }
  > .icon {
    flex-basis: 5%;
    > i {
      position: relative;
      display: inline-block;
      width: 2rem;
      height: 2rem;
      border-radius: 2px;
      padding: 0.5vh;
      background-color: #5090cd;
      box-sizing: border-box;
      > svg {
        color: #fff;
        width: 100%;
        height: 100%;
      }
    }
  }
  > .title {
    flex-basis: 50%;
    padding-left: 2vw;
    font-size: 1rem;
    overflow: hidden;
    border-bottom: 1px solid #e7e7e7;
  }
  > .tips {
    flex-grow: 1;
    font-size: .8rem;
    color: rgb(156, 156, 156);
    justify-content: flex-end;
    border-bottom: 1px solid #e7e7e7;
    > i {
      position: relative;
      display: inline-block;
      width: 1rem;
      height: 1rem;
      > svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}
.items{
    margin-top: 2vh;
}
</style>


<script>
export default {
  data(){
      return{
          itemsList:[
              {title:'购物车',path:'/cart',icon:'cart',bg:{backgroundColor:'#e7a200'}},
              {title:'搜索',path:'/cart',icon:'search',bg:{backgroundColor:'#5090cd'}},
              {title:'支付宝',path:'/cart',icon:'alipay',bg:{backgroundColor:'#00aeef'}},
              {title:'旺旺',path:'/cart',icon:'wangwang',bg:{backgroundColor:'#00aeef'}}
          ]
      }
  }
}
</script>



