<template>
    <div>
        <header class="logo"></header>
        <form class="form-login" action="/login" @submit.prevent>
            <div class="form-control">
                <input type="text" placeholder="手机号/邮箱/会员名" @keydown="showClearBtn('uname')" @focus="showClearBtn('uname')"  @blur="inputKeyup('uname')"  v-model="text.uname">
                <div class="clear-text" v-show="status.uname" @click="clearText('uname')">
                    <img src="/static/images/delete.png" alt="del">
                </div>
            </div>
            <div class="form-control"  >
                <input type="password" placeholder="请输入密码" @keydown="showClearBtn('pwd')" @focus="showClearBtn('pwd')" @blur="inputKeyup('pwd')" v-model="text.pwd">
                <div class="clear-text" v-show="status.pwd" @click="clearText('pwd')" >
                    <img src="/static/images/delete.png" alt="del">
                </div>
            </div>
            <div class="other-lk">
                <a href="/reg">免费注册</a>
                <a href="/forget">忘记密码</a>
            </div>
            <button type="submit" class="btn login-btn" @click='login'>登录</button>
            <button type="submit" class="btn msg-btn">短信验证码登录</button>
        </form>
    </div>
</template>

<style scoped>
body {
  background-color: #fff;
}
.logo {
  width: 6rem;
  height: 6rem;
  background: url("/static/images/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png")
    no-repeat;
  background-size: contain;
  margin: 5rem auto 0;
}
.form-login {
  position: relative;
  width: 80vw;
  margin: 0 auto;
}
.form-control {
  position: relative;
  border-bottom: 1px solid #ff5000;
  margin: 2rem 0 0 0;
  width: 100%;
}
.form-control input {
  border: none;
  background-color: inherit;
  outline: none;
  font-size: 1rem;
  width: 80%;
  height: 2rem;
  margin: 0 0 .5rem .5rem;
}
.other-lk {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
}
.other-lk a {
  font-size: 1rem;
}
.btn {
  height: 3.66rem;
  width: 100%;
  border-radius: 3rem;
  font-size: 1.4rem;

  text-align: center;
}
.btn:focus {
  outline: none;
}
.login-btn {
  border: none;

  color: #fff;
  background: -webkit-linear-gradient(left, #ff9000, #ff5000) no-repeat;
  box-shadow: 0 0.2rem 0.5rem #ff5000;
  letter-spacing: 1rem;
}
.msg-btn {
  margin-top: 2rem;
  color: #ff5000;
  background: #fff;
  border: 1px solid #ff5000;
}
.clear-text {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
<script>
export default {
  data() {
    return {
      text: {
        uname: "",
        pwd: ""
      },
      status: {
        uname: false,
        pwd: false
      }
    };
  },
  methods: {
    clearText(val) {
      this.text[val] = "";
      this.status[val] = false;
      console.log(this.text[val]);
    },
    showClearBtn(val) {
      if (!this.text[val]) return;
      this.status[val] = true;
    },
    inputKeyup(val) {
      this.status[val] = false;
    },
    login(){
        if(this.text.uname!=''&&this.text.pwd!=''){
            sessionStorage.setItem('username',this.text.uname);
            this.$router.go(-1);
        }
        
    }
  }
};
</script>


