<template>
  
  <div>
      <section id="idx_section">
        <dl class="product-list">
            <dd class="list-item"  v-for="(pro,index) in product" :key="pro.id">
                <div class="item-header">
                    <dl>
                        <dd>
                            <a href="javascript:void(0);">
                            </a>
                        </dd>
                        <dd>
                            <a href="javascript:void(0);" class="s-type-lk"></a>
                            <a href="javascript:void(0);" class="shop-lk">
                                {{ !pro.shop_name?'店铺名没有数据':pro.shop_name }}
                                <i class="fa fa-angle-right"></i>
                            </a>
                        </dd>
                        <dd>
                            <div class="edit-btn" @click="editItems(index)">{{editArr[index]?'完成':'编辑'}}</div>
                        </dd>
                    </dl>
                </div>
                <div class="s-item-con">
                    <dl class="s-item-list">
                        <dd class="p-item">
                            <div class="tips">
                                <a href="javascript:void(0);" :class='!selectIt[index]?"allitem":"allitem allitem-active"'  @click="selectThem(index)"></a>
                            </div>
                            <div class="item-img">
                                <img :src="pro.imgSrc?pro.imgSrc:'/static/images/tblogo.jpg'" alt="">
                            </div>
                            <div class="i-con">
                                    <h3 class="s-tit">
                                        {{pro.p_name}}
                                    </h3>
                                    <span class="s-group">{{pro.group}}</span>
                                    <div class="s-price">
                                        <div>
                                            <span class="now-price">￥{{pro.p_now_price}}.00</span>
                                            <span class='origin-price'>￥{{pro.p_origin_price}}.00</span>
                                        </div>
                                        <span class="s-quantity">X{{pro.p_num}}</span>
                                    </div>
                            </div>
                            <div class="edit-content" v-show="editArr[index]">
                                <div class="input-group">
                                    <a href="javascript:void(0);" class="input-group-addon" @click="decNum(index)"><span>-</span></a>
                                    <input type="text" class="form-control" name="p_num" v-model="pro.p_num">
                                    <a href="javascript:void(0);" class="input-group-addon" @click="addNum(index)">+</a>
                                </div>
                                    <a href='javascript:void(0);' class="del-box" @click="seen?seen=false:seen=true;thisIndex=index;">
                                        <span>删除</span>            
                                    </a>
                            </div>
                        </dd>
                    </dl>
                </div>
            </dd>
        </dl>
    </section>
    <!-- productList end -->
    <section class='end'></section>
    
    <!-- modal start -->
    <div class="modal" v-show="seen">
        <div class="modal-content">
            <div class="modal-header">
                <div class="modal-title">确认要删除该物品吗</div>
            </div>
            <div class="modal-body">
                <a href="javascript:void(0);" class="btn  btn-cancel" @click="seen=false">取消</a>
                <a href="javascript:void(0);" class="btn btn-confirm"  @click="seen=false;del(thisIndex)">确认删除</a>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
export default {
  data() {
    return {
        seen:false
    }
  },
  methods:mapActions(['selectThem','selectAll','editItems','del','addNum','decNum','toCounts']),
    computed:{
        ...mapGetters(['product','sAll','totalPrice','editArr','selectIt'])

    }
};
</script>



